    margin: 0px;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    font-family: "Montserrat", sans-serif;
}

li {
    list-style: none;
    width: 70px;
}

.headerimg {
    margin: 25px;
    width: 100%;
}    

    header,
    .container {
        max-width: 450px;
    }

    header nav a {
        font-size: 15px;
        font-weight: 500
    }

    .layout img {
        height: 111px;
    }

    .layout h1 {
        font-size: 24px;
    }

    .logements_container {
        background: none;
        margin: 3% 0;
        padding: 0;
        gap: 20px;
    }

    .logements_container a {
        width: 100%;
    }

    .logements_container figure {
        height: 255px;
    }

    .logements_container figure h3 {
        font-size: 18px;
    }

    .title-error {
        font-size: 86px;
    }

    .oups {
        font-size: 18px;
    }

    .logement_collapse {
        flex-direction: column;
    }

    .logement_informations {
        width: 100%;
    }

    .container-content {
        display: block;
    }

    .carousel__picture {
        height: 255px;
    }

    .host {
        display: flex;
        margin-top: 25px;
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: center;
    }

    .rating {
        padding-top: 20px;
    }
}
@media screen and (max-width:320px){
    header nav ul {
        display: inline;
        gap: 32px;
    }
}